// 
import * as types from './../mutation_types'
import {
  getServiceList,
  searchService,
  serviceOrderDetail,
  revokeService,
  returnPriceService,
  returnGoodsService,
  returnExpressEdit,
  deliverGoods
} from '@/api/service'
const CODE = 200
export default {
  state: {
    serverInfo: { // 
      status: 'fail',
      message: ''
    },
    // 
    orderInfo: {
      orderId: null, // ID,
      discountPrice: null, // ,
      id: null // ordersGoodsId id
    },
    // 
    returnGoodsInfo: {
      orderId: '', // id
      id: '', // id
      cover: '', // 
      name: '', // 
      number: '', // 
      skuDesc: '', // 
      cause: '', // 
      context: '', // 
      image: [] // 
    },
    // 0 ---> , 1 ---> 
    serviceType: null
  },
  mutations: {
    [types.GET_ORDER_INFO] (state, obj) { // 
      state.orderInfo = obj
    },
    [types.GET_SERVICE_TYPE] (state, type) { // 
      state.serviceType = type
    },
    [types.GET_SERVICE_RETURN_GOODS] (state, obj) { // 
      state.returnGoodsInfo = obj
    },
    [types.GLOBAL_RES] (state, {code, message, data}) {
      if (code !== CODE) {
        // vue.$vux.toast.text('')
        state.serverInfo = {
          status: 'fail',
          message
        }
        return false
      }
      state.serverInfo = {
        status: 'success',
        data
      }
    }
  },
  actions: {
    async getServiceList ({commit, state}, params) { // 
      let { data } = await getServiceList(params)
      commit('GLOBAL_RES', data)
      return state.serverInfo
    },
    async searchService ({commit, state}, params) { // 
      let { data } = await searchService(params)
      commit('GLOBAL_RES', data)
      return state.serverInfo
    },
    async serviceOrderDetail ({commit, state}, params) { // 
      let { data } = await serviceOrderDetail(params)
      commit('GLOBAL_RES', data)
      return state.serverInfo
    },
    async revokeService ({commit, state}, params) { // 
      let { data } = await revokeService(params)
      commit('GLOBAL_RES', data)
      return state.serverInfo
    },
    async returnPriceService ({commit, state}, params) { // 
      let { data } = await returnPriceService(params)
      commit('GLOBAL_RES', data)
      return state.serverInfo
    },
    async returnGoodsService ({commit, state}, params) { // 
      let { data } = await returnGoodsService(params)
      commit('GLOBAL_RES', data)
      return state.serverInfo
    },
    async returnExpressEdit ({commit, state}, params) { // 
      let { data } = await returnExpressEdit(params)
      commit('GLOBAL_RES', data)
      return state.serverInfo
    },
    async deliverGoods ({commit, state}, params) { // 
      let { data } = await deliverGoods(params)
      commit('GLOBAL_RES', data)
      return state.serverInfo
    }
  }
}



