import Axios from "@/util/request"

export const deleteAddress = data => Axios({ // 
  method: 'post',
  url: '/address/delete',
  data
})

export const getAddress = data => Axios({ // 
  method: 'post',
  url: '/address/list',
  data
})

export const setDefaultAddress = data => Axios({ // 
  method: 'post',
  url: '/address/api/address/set_default_address/',
  data
})

export const addAddress = data => Axios({ // 
  method: 'post',
  url: '/address/insert/',
  data
})

export const editAddress = data => Axios({ // 
  method: 'post',
  url: '/address/api/address/get_shipping_address_one/',
  data
})

export const getDefaultAddress = data => Axios({ // 
  url: '/address/api/address/get_default_address/',
  params: data
})



