import Axios from "@/util/request"
export const searchAssistantByShopId = data => Axios({ // id
  url: '/users/searchAssistantByShopId',
  method: 'post',
  data
})

export const viewAssistant = data => Axios({  // uid
  url: '/users/viewAssistant',
  method: 'post',
  data
})

export const usersView = data => Axios({  // uid
  url: '/users/view',
  method: 'post',
  data
})

export const seachShopAssistant = data => Axios({  // idid id
  url: '/users/seachShopAssistant',
  method: 'post',
  data
})

// 
export const queryBindAssistant = data => Axios({  // 
  url: '/users/queryBindAssistant',
  method: 'post',
  data
})

// 
export const chooseAdviser = data => Axios({  // 
  url: '/users/bindAssistant',
  method: 'post',
  data
})

// 
export const relieveAdviser = data => Axios({  // 
  url: '/users/freeAssistant',
  method: 'post',
  data
})



